<template>
    <div>
        <button  @click="send" class="btn">Start</button>
    </div>
</template>
  
<script>
import axios from 'axios';
export default {
    name: "start_bot",
    props: {
        name_bot: String,

    },

    methods: {
        async send() {
            // try {
                const data_to_start_bot = {
                    botname: this.name_bot
                }
                console.log(data_to_start_bot.botname)
                const response = await axios.post("http://127.0.0.1:8000/startbot/",
                    JSON.stringify(data_to_start_bot));
                    // console.log("Response status:", response.status); 
                if (response.status === 200) {
                    alert(`"${data_to_start_bot.botname}" is Started Sucessfully!`); 
                }else{
                    alert("Bot Unable to Run!");

                }

        // } catch(err) {
        //     console.log(err)
        // }
    }


}

};
</script>
