<template>
    <div>
        <!-- <input type="text" placeholder="bot name" v-model="b_name"> -->
        <button @click="dellete" class="btn">Delete</button>
    </div>
</template>
<script>
import axios from 'axios';
export default{
    name:"dell_bot",
    data(){
       return{
        // b_name:""
       } 
    },
    props:{
        name_bot:String
    },

    methods: {
      
        async dellete() {
           if(confirm(`Are You Sure To Delete ${this.name_bot}`)){
            // try {
                const dell = {
                    bot_name: this.name_bot
                }
                const del=await axios.post("http://127.0.0.1:8000/delete/", JSON.stringify(dell));
                console.log("deleted");
                if (del.status === 200) {
                    this.$parent.refreshBotList();
                    alert(`Bot "${dell.bot_name}" Delleted successfully!`);
                    }
                    else{
                        alert("Bot is not Deleted yet")}
                    
            // } catch(error) {
            //     console.log(error);
            // }
           }
           else{
            alert(`You Saved The "${this.name_bot}" Bot,Successfully!`)
           }
        }
    }
}
</script>
